<template>
    <div class="full">
        <draggable element="div"
                   class="construct-form__photo"
                   v-model="files"
                   @start="dragStart"
                   @end="dragSave"
                   :move="dragStatus"
                   v-if="typeof(files) !== 'undefined'"
                   :group="{ name: fieldName }"
                   :draggable="files.length > 1 ? '.item-wrap-draggable'+fieldName:false"
                   :ghostClass="'sortable-ghost-custom'"
                   :chosenClass="'sortable-chosen-custom'"
                   :dragClass="'sortable-drag-custom'"
                   :dataIdAttr="'data-id'"
                   :filter="'.disable-drag-edit'"
                   :animation="150"
                   :touchStartThreshold="1"
                   :delay="0"
        >

                <div :class="['item-wrap-draggable'+fieldName,{'item-wrap-draggable':files.length > 1}]" class="item-wrap" v-for="f in files" :key="f.id">
                    <div class="item --img"
                         :style="{'background-image': 'url(//api.zemskidki.ru/'+f.icon+')'}">
                        <span class="item__remove" @click="deleteFile(f.id)"></span>

                        <tooltip  v-if="isMove" :direction="'bottom-left-direction'"
                                  :add-class-wrap="'item__move-tooltip'"
                                  style="z-index: 11;"
                                  :add-style="{width: 267 + 'px'}">
                            <span slot="tooltipBtn" class="item__move-btn"></span>

                            <div slot="tooltipContent">
                                <div class="text-bold text-size-m">Переместить в...</div>

                                <div class="text-size-m mt16">
                                    <div v-if="moveTo.code !== fieldName" v-for="moveTo in moveToList" class="mt16"><a  @click="moveFile(f.id, moveTo.code)" href="javascript:void(0);">{{moveTo.name}}</a></div>
                                </div>
                            </div>
                        </tooltip>
                    </div>
                </div>

                <div class="item-wrap" v-if="files.length < maxFiles">
                    <div class="item --add"
                         :class="{error:error}"
                         @click="errorPopupShow = false;openInput();">
                        <div class="icon">
                            <span v-if="!isSend" class="i i-photo"></span>
                            <span v-else class="preloader"></span>
                        </div>

                        <span v-if="!isSend" class="t">Добавить фото</span>
                    </div>
                </div>

                <div class="item-wrap" v-for="n in countEmpty" v-if="files.length < maxFiles">
                    <div class="item --blank"></div>
                </div>
        </draggable>




        <input v-if="!isSend" :accept="acceptType" style="display: none;" ref="fileInput" multiple @change="onFileChange" type="file">





        <popup @close="close('errorPopupShow')" :show="errorPopupShow" :width="550">
            <div id="popup-cropper-warning">
                <div class="text-size-xl text-color-red text-bolder">
                    Ошибка загрузки!
                </div>


                <div class="popup__content">
                    <div>Ширина изображения не должна быть меньше 800px.</div>
                    <div>Высота изображения не должна быть меньше 600px.</div>
                    <div>Вес изображения не должен превышать 10Мб.</div>
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>

        <popup :is-btn-close="false"
               @close="close('intermediateStep')"
               ref="intermediateStepPopup"
               :width="736"
               :show="intermediateStep">
            <div class="popup__header mb">
                <div class="title">Загрузка фотогалереи</div>
                <div class="mt16">Все загружаемые изображения необходимо обрезать под формат 4:3.</div>
            </div>

            <div class="popup__content mt32">
                <div class="construct-form__photo">
                        <div class="item-wrap" v-for="f in filesTemp" :key="f.id">
                            <div class="item --img"
                                 :style="f.style"
                                 :class="{'--no-format':f.isError}">
                                <span class="item__remove" @click="deleteFileTemp(f.id)"></span>

                                <div class="item__no-format" v-if="f.isError">
                                    <div class="icon">
                                        <span class="i i-warning i--white"></span>
                                    </div>

                                    <span class="t">Недопустимый формат</span>
                                </div>
                            </div>
                        </div>

                    <div class="item-wrap" v-if="files.length + filesTemp.length < maxFiles">
                        <div class="item --add"
                             @click="openInput();">
                            <div class="icon">
                                <span class="i i-photo"></span>
                            </div>

                            <span class="t">Добавить фото</span>
                        </div>
                    </div>
                    <div class="item-wrap" v-for="n in countEmptyTemp" v-if="files.length + filesTemp.length < maxFiles">
                        <div class="item --blank"></div>
                    </div>
                </div>
            </div>

            <div class="popup__footer">
                <button class="btn" @click="openCropper()">Продолжить</button>
                <button class="btn btn--grey ml32" @click="hideIntermediateStep">Отмена</button>
            </div>

        </popup>

        <cropper @crop="onFileCrop"
                 @hide="cropperVisible = false"
                 @close="cropperShow = false"
                 @cancel="cropperHide()"
                 :prop-popup-show="cropperShow"
                 v-show="cropperVisible"
                 :count="filesTemp.length"
                 :current="filesTempCurrent"
                 v-if="targetFile && cropperShow"
                 :type="targetFileType"
                 :src="targetFile"
                 :width="targetFileWidth"
                 :height="targetFileHeight"></cropper>
    </div>
</template>

<script>

    import {declOfNumber} from '@/helpers/formatted';
    import Cropper from '@/components/forms/Cropper.vue';
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import draggable from '@/helpers/vuedraggable.js';

    export default {
        name: "FileUploadCropperGalleryMulti",
        components: {
            Cropper,
            Popup,
            Tooltip,
            draggable
        },
        props: {
            maxFiles: {default: 100},
            acceptType: {default: ''},
            maxFilesSize: {default: 10*1024*1024},
            hintSolo: {default: 'Минимальный размер — 800х600px, максимальный вес — 10Мб'},
            btnText: {default: 'Загрузить изображение'},
            path: {default: false},
            value: {default: []},
            moveToList: {default(){
                return []
            }},
            fieldName: {default: 'file'},
            actionDelete: {type: String,required: true},
            actionSend: {type: String,required: true},
            error: {default: false},
            elementId:  {default: false},
            isModeraite:  {default: false},
            isMove:  {default: false},
            minWidth: {default: 800},
            minHeight: {default: 600}
        },

        data: function () {
            return {
                file: {},
                files: [],
                filesTemp: [],
                filesTempCurrent: 1,
                fileInput: false,
                fileId: false,
                isSend: false,
                isSendCount: 0,
                cropperShow: false,
                errorPopupShow: false,
                cropperVisible: false,
                targetFile: false,
                targetFileWidth: false,
                targetFileHeight: false,
                targetFileType: false,
                ajax: false,
                intermediateStep: false,
            }
        },

        computed: {
            countEmpty() {
                if(this.files.length === 0) {
                    return 3
                } else if(this.files.length < 4){
                    return 3 - this.files.length > 0 ? 3 - this.files.length : 0;
                } else {
                    return 3 - (this.files.length % 4) > 0 ? 3 - (this.files.length % 4) : 0;
                }
            },
            countEmptyTemp() {
                if(this.filesTemp.length === 0) {
                    return 3
                } else if(this.filesTemp.length < 4){
                    return 3 - this.filesTemp.length > 0 ? 3 - this.filesTemp.length : 0;
                } else {
                    return 3 - (this.filesTemp.length % 4) > 0 ? 3 - (this.filesTemp.length % 4) : 0;
                }
            }
        },

        watch: {
            value: function () {
                this.$nextTick(function () {
                    this.files = [...this.value];
                });
            },
            filesTemp(){
                this.$nextTick(function () {
                    this.$refs.intermediateStepPopup.updateViewportSize()
                })
            }
        },

        mounted: function () {
            this.files = [...this.value];
        },

        methods: {
            dragSave: function (e) {
                //console.log(e)
                this.$emit('input', this.files);
            },
            dragStart: function (e) {
                //console.log(e)
            },
            dragStatus: function (e) {
                //console.log(e)
            },
            hideIntermediateStep(){
                this.intermediateStep = false;
                this.filesTempCurrent = 1;
                this.filesTemp = [];
            },
            cropperHide(){
                this.filesTempCurrent = 1;
                this.filesTemp = [];
                this.cropperShow = false;
            },
            moveFile(id, to){
                let file = false;
                for(let f in this.files){
                    if(+this.files[f].id === +id){
                        file = this.files[f];
                        this.files.splice(f , 1);
                    }
                }
                if(file) {
                    file.move = true;
                    this.$emit('move', {
                        files:this.files,
                        move: {
                            file:file,
                            to:to
                        }
                    });
                }
            },
            deleteFile(id){
                for(let f in this.files){
                    if(+this.files[f].id === +id){
                        if(this.actionDelete !== 'false'){
                            this.$store.dispatch(this.actionDelete, {
                                fileId: id,
                                fieldName:this.fieldName,
                                isModeraite: this.isModeraite,
                                actionId:+this.elementId > 0 ? this.elementId : false
                            }).then((e)=> {
                                this.files.splice(f , 1);
                                this.$emit('input', this.files);
                            });
                        } else {
                            this.files.splice(f , 1);
                            this.$emit('input', this.files);
                        }
                    }
                }

            },
            deleteFileTemp(id){
                for(let f in this.filesTemp){
                    if(this.filesTemp[f].id === id){
                        this.filesTemp.splice(f , 1);
                    }
                }
                if(this.filesTemp.length === 0){
                    this.close('intermediateStep');
                }
            },
            getViewportSize: function () {
                let d = window.document.querySelector("html");
                return { w: d.clientWidth, h: d.clientHeight };
            },

            close(name){
                this[name] = false;
                if(!this.intermediateStep){
                    document.getElementsByTagName('body')[0].classList.remove("noscroll");
                }
            },
            openInput: function() {
                if(!this.isSend) {
                    this.close('errorPopupShow');
                    this.$refs.fileInput.click();
                }
            },

            openCropper(){
                for(let f in this.filesTemp){
                    if(this.filesTemp[f].isError === true){
                        this.filesTemp.splice(f , 1);
                    }
                }
                if(this.filesTemp.length === 0){
                    this.close('intermediateStep');
                } else {
                    let file = this.filesTemp[this.filesTempCurrent - 1];
                    this.targetFileHeight = file.img.height;
                    this.targetFile = file.img.src;
                    this.targetFileWidth = file.img.width;
                    this.targetFileType = file.type;

                    this.$emit('change');
                    this.cropperShow = true;
                    this.cropperVisible = true;
                    this.fileInput = file.file;
                    this.fileId = file.id;
                    this.intermediateStep = false;
                }
            },

            onFileChange: function(e) {
                let self = this;
                let files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                if(files.length > 1 || self.intermediateStep) {
                    if(!self.intermediateStep) {
                        self.filesTemp = [];
                    }
                    for(let i in files){
                        if(files[i].size !== undefined){
                            let URL = window.URL || window.webkitURL;
                            let img = new Image();
                            img.src = URL.createObjectURL(files[i]);
                            img.onload = function () {
                                if(this.height < self.minHeight || this.width < self.minWidth || files[0].size > 10 * 1024 * 1024) {
                                    self.filesTemp.push({
                                        img: this,
                                        file: files[i],
                                        src: img.src,
                                        isError: true,
                                        style: {
                                            'background-image': 'url(' + img.src + ')',
                                            'background-position': 'top center',
                                        },
                                        id: Math.random().toString(36).substr(2, 9)
                                    });
                                } else {
                                    self.filesTemp.push({
                                        img: this,
                                        file: files[i],
                                        src: img.src,
                                        isError: false,
                                        style: {
                                            'background-image': 'url(' + img.src + ')',
                                            'background-position': 'top center',
                                        },
                                        id: Math.random().toString(36).substr(2, 9)
                                    });
                                }
                            };
                        }
                    }
                    this.intermediateStep = true;
                    this.$nextTick(function () {
                        this.$refs.intermediateStepPopup.updateViewportSize()
                    })
                } else {
                    let URL = window.URL || window.webkitURL;
                    self.targetFile = URL.createObjectURL(files[0]);

                    let img = new Image();
                    img.src = self.targetFile;
                    img.onload = function () {
                        if(this.height < self.minHeight || this.width < self.minWidth || files[0].size > 10 * 1024 * 1024){
                            self.errorPopupShow = true;
                        } else {
                            self.targetFileHeight = this.height;
                            self.targetFileWidth = this.width;
                            self.targetFileType = files[0].type;

                            self.$emit('change');
                            self.cropperShow = true;
                            self.cropperVisible = true;
                            self.fileInput = files[0];
                        }

                    };
                }
            },

            onFileCrop: function(blob) {
                this.file = {
                    id: false,
                    name: this.fileInput.name,
                    size: this.fileInput.size,
                    path: false,
                    icon: false,
                    finish: false,
                    ext: this.getExt(this.fileInput.name),
                    progress: 0,
                };
                this.fileUpload(blob);
                if(this.filesTemp.length > 1) {

                    for(let f in this.filesTemp){
                        if(this.filesTemp[f].id === this.fileId){
                            let w = 152;
                            let h = 117;
                            let sW = w / (this.filesTemp[f].img.width);
                            let l = -(blob.x * sW);
                            let sH = h / (this.filesTemp[f].img.height);
                            let t = -(blob.y * sH);
                            let bW = this.filesTemp[f].img.width - blob.width;
                            this.filesTemp[f].crop = blob;
                            this.filesTemp[f].style['background-position'] = l+'px '+t+'px';
                            this.filesTemp[f].style['background-size'] = w/sW+'px auto';
                            this.filesTempCurrent++;
                            if(this.filesTempCurrent <= this.filesTemp.length){
                                this.$nextTick(()=>{
                                    this.openCropper();
                                    document.getElementsByTagName('body')[0].classList.add("noscroll");
                                })
                            } else {
                                this.$nextTick(()=>{
                                    this.filesTempCurrent = 1;
                                    if(!this.intermediateStep) {
                                        this.filesTemp = [];
                                    }
                                })

                            }
                        }
                    }


                } else {

                }

            },

            filesTempUpload(){
                for(let f in this.filesTemp){
                    this.fileInput = this.filesTemp[f].crop;
                    this.fileUpload(this.filesTemp[f].crop);
                }
            },

            getExt: function(name) {
                let parts;
                return ( parts = name.split(".") ).length > 1 ? parts.pop() : "";
            },

            fileUpload: function (crop) {
                let self = this;
                let form_data = new FormData();
                form_data.append(self.fieldName.toString(), this.fileInput, this.fileInput.name);
                form_data.append("crop[x]", crop.x < 0 ? 0 : crop.x);
                form_data.append("crop[y]", crop.y < 0 ? 0 : crop.y);
                form_data.append("crop[w]", crop.width);
                form_data.append("crop[h]", crop.height);

                //if(!self.isSend) {
                    self.isSend = true;
                    self.isSendCount++;
                    this.$store.dispatch(this.actionSend, {
                            form_data:form_data,
                            fieldName:this.fieldName,
                            isModeraite: this.isModeraite,
                            elementId:+this.elementId > 0 ? this.elementId : false
                        }).then(function(f) {
                        self.isSendCount--;
                        if(self.isSendCount === 0){
                            self.isSend = false;
                        }
                        let file = {
                            id: f.id,
                            name: self.file.name,
                            size: self.file.size,
                            path: f.path,
                            finish: true,
                            icon: f.icon,
                            ext: self.file.ext,
                        };
                        self.files.push(file);
                        self.$emit('input', self.files);
                    }).catch(function (error) {

                    });
                //}

            }
        }
    }
</script>

<style>
    .item-wrap-draggable .item.--img{
        cursor: grab;
    }
    .item-wrap-draggable.sortable-chosen-custom .item.--img{
        cursor: grabbing;
    }
    .item-wrap-draggable.sortable-chosen-custom .item__remove{
        display: none;
    }
</style>
