<template>
    <div>
        <div class="construct-form__video">
            <div class="item-wrap" v-for="(video,i) in videos">
                <div class="item --video" :style="{ backgroundImage: 'url('+video.preview+')' }">
                    <span class="item__remove" @click="deleteVideo(i)"></span>
                </div>
            </div>

            <div class="item-wrap">
                <div class="item --add"
                     :class="{error: error}"
                     @click="openVideoAdd">
                    <div class="icon">
                        <span class="i i-video"></span>
                    </div>

                    <span class="t">Добавить видео</span>
                </div>
            </div>

            <div class="item-wrap" v-for="n in countEmpty">
                <div class="item --blank"></div>
            </div>

        </div>
        <!-- Добавить видео -->
        <popup @close="modal = false" :show="modal">
            <construct-video-add @add="addVideo"></construct-video-add>
        </popup>
    </div>

</template>

<script>
    import {mapState} from 'vuex'
    import ConstructVideoAdd from '@/components/modal/ConstructVideoAdd.vue'
    import Popup from '@/components/modal/Popup.vue';
    export default {
        name: "VideoGalleryAdd",
        components: {
            ConstructVideoAdd,
            Popup,
        },
        props: {
            value: {
                default(){
                    return []
                }
            },
            error: {
                default: false
            },
            actionId: {
                default: false
            },
        },
        data() {
            return {
                videos: [],
                modal: false,
            }
        },
        watch: {
            value: function () {
                this.$nextTick(function () {
                    this.videos = [...this.value];
                });
            }
        },

        mounted: function () {
            this.videos = [...this.value];
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            countEmpty() {
                if(this.videos.length === 0) {
                    return 3
                } else if(this.videos.length < 4){
                    return 3 - this.videos.length > 0 ? 3 - this.videos.length : 0;
                } else {
                    return 3 - (this.videos.length % 4) > 0 ? 3 - (this.videos.length % 4) : 0;
                }
            }
        },
        methods: {
            openVideoAdd: function () {
                setTimeout(()=>{
                    this.modal = true;
                },10)
            },
            deleteVideo(i){
                this.videos.splice(i , 1);
                this.$emit('input', this.videos);
            },
            addVideo(video) {
                this.videos.push(video);
                this.$emit('input', this.videos);
                this.modal = false;
            }
        }
    }
</script>

