<template>
    <div style="display: flex;">
        <div @click="errorPopupShow = false;openInput();" v-if="file.icon"
             class="coupon__img --add-img"
             :class="{error:error}"
             :style="{'background-image': 'url(//api.zemskidki.ru/'+file.icon+')'}">
            <span class="coupon__img__add">
                 <span class="icon">
                     <span class="i i-photo" style="transform: scale(2)" v-if="!isSend"></span>
                     <span v-else class="preloader"></span>
                </span>
                <span class="t">Загрузить изображение</span>
            </span>
        </div>
        
        
        <div v-else @click="errorPopupShow = false;openInput();"
             class="coupon__img --add-img">
            <span class="coupon__img__add" :class="{error:this.error}">
                <span class="icon">
                    <span class="i i-photo" style="transform: scale(2)" v-if="!isSend"></span>
                     <span v-else class="preloader"></span>
                </span>
                <span class="t">Загрузить изображение</span>
            </span>
        </div>
        
        
        <input v-if="!isSend" :accept="acceptType" style="display: none;" ref="fileInput" @change="onFileChange" type="file">
        
        
        <cropper @crop="onFileCrop"
                 @hide="cropperVisible = false"
                 @close="cropperShow = false"
                 @cancel="cropperHide()"
                 :prop-popup-show="cropperShow"
                 v-show="cropperVisible"
                 v-if="targetFile && cropperShow"
                 :type="targetFileType"
                 :src="targetFile"
                 :width="targetFileWidth"
                 :height="targetFileHeight"></cropper>


        <popup @close="close()" :show="errorPopupShow" :width="720">
            <div id="popup-cropper-warning">
                <div class="text-size-xl text-color-red text-bolder">
                    Размер изображения должен быть <br>
                    не меньше 800x600px
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>
        <popup @close="close()" :show="errorSizePopupShow" :width="720">
            <div>
                <div class="text-size-xl text-color-red text-bolder">
                    Вес изображения не должен быть <br>
                    более 10Мб
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>
        <popup @close="close()" :show="errorTypePopupShow" :width="720">
            <div>
                <div class="text-size-xl text-color-red text-bolder">
                    Допускаются только форматы изоборажения JPG или PNG
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>
    </div>
</template>

<script>

    import {declOfNumber} from '@/helpers/formatted';
    import Cropper from '@/components/forms/Cropper.vue';
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';

    export default {
        name: "FileUploadCropper",
        components: {
            Cropper,
            Popup,
        },
        props: {
            maxFiles: {default: 1},
            acceptType: {default: ''},
            maxFilesSize: {default: 10*1024*1024},
            hintSolo: {default: 'Минимальный размер — 800х600px, максимальный вес — 10Мб'},
            btnText: {default: 'Загрузить изображение'},
            path: {default: false},
            value: {default: {}},
            fieldName: {default: 'file'},
            actionDelete: {type: String,required: true},
            actionSend: {type: String,required: true},
            error: {default: false},
            elementId:  {default: false},
            minWidth: {default: 800},
            minHeight: {default: 600}
        },

        data: function () {
            return {
                file: {},
                fileInput: false,
                isSend: false,
                cropperShow: false,
                errorTypePopupShow: false,
                errorPopupShow: false,
                errorSizePopupShow: false,
                cropperVisible: false,
                targetFile: false,
                targetFileWidth: false,
                targetFileHeight: false,
                targetFileType: false,
                ajax: false,
            }
        },

        computed: {

        },

        watch: {
            value: function () {
                this.$nextTick(function () {
                    this.file = this.value;
                });
            }
        },

        mounted: function () {
            this.file = this.value;
        },

        methods: {
            cropperHide(){
                this.cropperShow = false;
            },
            getViewportSize: function () {
                let d = window.document.querySelector("html");
                return { w: d.clientWidth, h: d.clientHeight };
            },
            close(){
                this.errorPopupShow = false;
                this.errorSizePopupShow = false;
                this.errorTypePopupShow = false;
                document.getElementsByTagName('body')[0].classList.remove("noscroll");
            },
            openInput: function() {
                if(!this.isSend) {
                    this.close();
                    this.$refs.fileInput.click();
                }
            },

            onFileChange: function(e) {
                let self = this;
                let files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                let URL = window.URL || window.webkitURL;
                self.targetFile = URL.createObjectURL(files[0]);

                let img = new Image();
                img.src = self.targetFile;
                img.onload = function () {
                    if(this.height < self.minHeight || this.width < self.minWidth || files[0].size > 10 * 1024 * 1024){
                        if(this.height < self.minHeight || this.width < self.minWidth){
                            self.errorPopupShow = true;
                        }
                        if(files[0].size > 10 * 1024 * 1024){
                            self.errorSizePopupShow = true;
                        }
                        
                    } else {
                        self.targetFileHeight = this.height;
                        self.targetFileWidth = this.width;
                        self.targetFileType = files[0].type;

                        self.$emit('change');
                        self.cropperShow = true;
                        self.cropperVisible = true;
                        self.fileInput = files[0];
                    }

                };


            },

            onFileCrop: function(blob) {
                this.file = {
                    id: false,
                    name: this.fileInput.name,
                    size: this.fileInput.size,
                    path: false,
                    icon: false,
                    finish: false,
                    ext: this.getExt(this.fileInput.name),
                    progress: 0,
                };
                this.fileUpload(blob);
            },

            getExt: function(name) {
                let parts;
                return ( parts = name.split(".") ).length > 1 ? parts.pop() : "";
            },

            fileUpload: function (crop) {
                let self = this;
                let form_data = new FormData();
                form_data.append(self.fieldName.toString(), this.fileInput, this.fileInput.name);
                form_data.append("crop[x]", crop.x < 0 ? 0 : crop.x);
                form_data.append("crop[y]", crop.y < 0 ? 0 : crop.y);
                form_data.append("crop[w]", crop.width);
                form_data.append("crop[h]", crop.height);

                if(!self.isSend) {
                    self.isSend = true;

                    this.$store.dispatch(this.actionSend, {
                            form_data:form_data,
                            elementId:+this.elementId > 0 ? this.elementId : false
                        }).then(function(f) {
                        self.isSend = false;
                        let file = {
                            id: f.id,
                            name: self.file.name,
                            size: self.file.size,
                            path: f.path,
                            finish: true,
                            icon: f.icon,
                            ext: self.file.ext,
                        };
                        self.file = file;
                        self.$emit('input', file);
                    }).catch(function (f) {
                        self.errorTypePopupShow = true;
                        self.$emit('input', false);
                        self.isSend = false;
                    });
                }

            }
        }
    }
</script>

