<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8">Добавить видео</div>
            
            <div>Укажите ссылку на страницу видеозаписи на Youtube</div>
            
            <div class="mt40">
                <input-styled :placeholder="'Ссылка на видео'"
                              name="video"
                              :placeholder-input="'http://www.workprotools.com/catalog'"
                              :isError="error"
                              ref="videoInput"
                              @focus="error = false"
                              v-model="video.url"
                              :errorText="'Видеосервис не поддерживается, либо ссылка является неправильной'"></input-styled>
            </div>
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" @click="add">Добавить видео</button>
                </div>
                
                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import InputStyled from '@/components/forms/InputStyled.vue';

    export default {
        name: "ConstructVideoAdd",
        components: {
            InputStyled,
        },
        props: {
        },
        data: function() {
            return {
                error: false,
                video: {
                    url: ''
                }
            }
        },
        created() {

        },
        mounted() {
            this.error = false;
            this.video = {
                url: ''
            };
            this.$nextTick(function () {
                this.$refs.videoInput.$el.getElementsByClassName('inp-s')[0].focus()
            })
        },
        methods: {
            add() {
                if(this.checkUrl(this.video.url)){
                    let id = this.video.url.match(/http(?:s?):\/\/(?:www\.)?youtu(?:be\.com\/watch\?v=|\.be\/)([\w\-\_]*)(&(amp;)?[\w\?=]*)?/)[1];
                    if(id){
                        this.$emit('add', {
                            url: 'https://www.youtube.com/embed/'+id,
                            preview: '//img.youtube.com/vi/'+id+'/0.jpg',
                            id: id,
                        });
                        this.$store.commit('popups/close', {name: this.popups.constructVideoAdd.name});
                    } else {
                        this.error = true;
                    }
                }
            },
            checkUrl(url){
                if(url.trim().length === 0){
                    this.error = true;
                    return false;
                }
                if(!/(?:https?:\/\/)?(?:www\.)?youtu\.?be(?:\.com)?\/?.*(?:watch|embed)?(?:.*v=|v\/|\/)([\w\-_]+)\&?/.test(url.trim())){
                    this.error = true;
                    return false;
                }
                return true;
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

