<template>
    <!-- плагин https://github.com/fengyuanchen/cropperjs/blob/master/README.md -->
    <popup :width="cropperParams.cropWidthPopup"
           @close="cancel"
           :is-set-blank="true"
           :is-resize="false"
           :show="true" :add-class="'popup--cropper'">
            <div class="popup-img-crop">
                <div class="img-cropper">
                    <div class="img-cropper__title">Кадрирование изображения <span v-if="count > 1">{{current}} из {{count}}</span></div>
                    <div class="img-cropper__subtitle">Двигайте желтые точки так, чтобы в кадре поместилось всё что нужно и ничего лишнего...</div>

                    <div class="img-cropper__image-wrap">
                        <div class="img-cropper__image" :style="{width: cropperParams.cropWidth+'px'}">
                            <img :src="src" ref="img" class="cropper-img cropper-hidden">
                        </div>
                    </div>

                    <div class="img-cropper__bottom">
                        <div>
                            <a @click="getData" href="javascript:void(0);" class="btn">Продолжить</a>
                        </div>
                    </div>
                </div>
            </div>
    </popup>
</template>

<script>
    import Cropper from 'cropperjs';
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';

    export default {
        name: "Cropper",
        components: {
            Popup
        },
        props: {
            'containerStyle': Object,
            'propPopupShow': {
                type: Boolean,
                default: false
            },
            'src': {
                type: String,
                default: ''
            },
            'type': {
                type: String,
                default: ''
            },
            'width': {
                default: ''
            },
            'height': {
                default: ''
            },
            'aspectRatio': {
                default: 4 / 3
            },
            'minCropBoxWidth': {
                default: 800
            },
            'minCropBoxHeight': {
                default: 600
            },
            'count': {
                default: 1
            },
            'current': {
                default: 1
            },
        },
        mounted() {
            this.cropperParams.aspectRatio = this.aspectRatio;
            this.cropperParams.minCropBoxWidth = this.minCropBoxWidth;
            this.cropperParams.minCropBoxHeight = this.minCropBoxHeight;
            this.$nextTick(function () {
                this.$el.querySelector('.popup-img-crop').style.width = this.cropperWidth();
                let self = this;
                setTimeout(function () {
                    self.cropper = new Cropper(self.$refs.img, self.cropperParams);
                },100);
            });
        },
        watch: {
            /*propPopupShow: function (oldValue, newValue) {
                if(this.propPopupShow == true) {
                    this.$nextTick(function () {
                        let wrap = this.cropperWidth(this.$refs.img);
                        $(this.$el).find('.popup-img-crop').width(wrap);
                        this.cropper = new Cropper(this.$refs.img, this.cropperParams);
                    });

                }
            }*/
        },
        data: function () {
            let self  = this;
            return {
                show: true,
                cropper: {},
                cropperParams: {
                    minCropBoxWidth: 800,
                    minCropBoxHeight: 600,
                    minCanvasWidth: 800,
                    minCanvasHeight: 600,
                    maxCanvasWidth: 1100,
                    maxCanvasHeight: 600,
                    autoCropArea: 1,
                    cropWidth: 600,
                    cropWidthPopup: 710,
                    zoomable: false,
                    guides: false,
                    center: true,
                    viewMode: 1,
                    dragMode: 'none',
                    aspectRatio: 4 / 3,
                    crop: function (event) {
                        let $el = self.$el.querySelector('.cropper-center');
                        $el.innerHTML = '<span>Минимальный размер 800х600px</span>';

                        if (event.detail.width <= 910) {
                            self.$el.classList.remove('active');;
                        } else {
                            self.$el.classList.add('active');;
                        }
                    }
                },
            }
        },
        methods: {
            cancel: function() {
                this.cropper.destroy();
                this.$emit('cancel');
                document.getElementsByTagName('body')[0].classList.remove("noscroll");
            },
            hidePopup: function() {
                this.cropper.destroy();
                this.$emit('close');
                document.getElementsByTagName('body')[0].classList.remove("noscroll");
            },
            getData: function() {
                let self = this;
                self.$emit('crop',this.cropper.getData());
                self.hidePopup();
            },
            cropperWidth: function() {
                document.getElementsByTagName('body')[0].classList.add("noscroll");

                this.$nextTick(function () {
                    let hRatio = Math.trunc((600 / this.height) * 100), //% фикс. высоты от максимальной высоты изображения
                        cropWidth = Math.trunc((this.width / 100) * hRatio);
                    let self = this;

                    if(cropWidth > this.cropperParams.maxCanvasWidth) {
                        cropWidth = this.cropperParams.maxCanvasWidth;
                    }
                    this.cropperParams.minCropBoxWidth = (this.minCropBoxWidth * hRatio) / 100;//расчет минимальной области выделения (если изображение не умещается и оно уменьшено)
                    this.cropperParams.minCropBoxHeight = (this.minCropBoxHeight * hRatio) / 100;
                    this.cropperParams.cropWidth = cropWidth;
                    this.cropperParams.cropWidthPopup = cropWidth;


                    if(cropWidth < 710) {
                        this.cropperParams.cropWidthPopup = 710;
                    }

                });
            },

        }
    }
</script>

