<template>
    <div :class="{open:opened}" style="position: relative; ">
        <transition name="fade">
            <div class="construct-form__check-title" v-if="!(isHideTitleOpen && opened)">
                <div class="pseudo-label" @click="openItem">
                    {{ propTitle }}
                </div>
            </div>
        </transition>
        
        <transition
                @enter="enterSlideUp"
                @leave="leaveSlideUp"
                :css="false">
            <div v-show="opened" class="wrap-hidden">
                <slot></slot>
            </div>
        </transition>
    </div>
    <!-- end item -->
</template>


<script>
    import Velocity from 'velocity-animate';

    export default {
        name: "ConstructCheckShow",
        props: {
            value: {default: false},
            isHideTitleOpen: {default: false},
            propTitle: {default: ''},
            propId: {default: ''},
        },

        data: function () {
            return {
                opened: false,
            }
        },

        computed: {},

        created: function () {
            this.$set(this, 'opened', !!this.value);
            this.$emit('input', this.opened);
        },

        mounted: function () {

        },

        methods: {
            openItem(){
                this.opened = !this.opened;
                this.$emit('input', this.opened);
                this.$emit('open', this.opened);
            },
            enterSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideDown", { duration: 300 }).then(function(e) { done(); });
            },

            leaveSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideUp", { duration: 300 }).then(function(e) { done(); });
            },
            enterFade: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideDown", { duration: 300 }).then(function(e) { done(); });
            },
            leaveFade: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideUp", { duration: 300 }).then(function(e) { done(); });
            },
        }
    }
</script>

<style scoped>
    .fade-enter-active, .fade-leave-active {
      transition: opacity .25s;
    }
    .fade-leave-active {
        transition-duration: 0s;
    }
    
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
</style>